      id: 3,
      title: "Arnau Vidal on Team Heretics not making Call of Duty League",
      text:
        "Team Heretics became one of the most popular names in Call of Duty esports in the past year, building a solid reputation for itself with its Spanish roster. A big change was impending upon the scene, however, in the form of franchising.The Spanish organisation applied to acquire one of the 12 franchise slots in the Call of Duty League but wasn’t successful, despite getting rather close. The sad news was broken to fans on September 14th and we later saw that ReKTGlobal and c0ntact Gaming acquired the two European slots for the inaugural season.Esports Insider sat down with Arnau Vidal, Founder & Co-owner of Team Heretics to find out about decision, what the organisation has been doing since, and his thoughts on how the league is shaping up.Esports Insider: Can you tell us about Activision Blizzard’s decision to not include you in the league?Arnau Vidal: We were excited about the idea of getting a franchise team for for Call of Duty. We believe that it’s the right path for us. It’s the right path for esports. I think that Activision Blizzard is really good at selling commercial deals that drive revenue to the league so I think that it’s going to be a good product.Specifically for us, we were born as a small Call of Duty team in Spain, we have grown a lot since, but we come from Call of Duty and most of our fan base comes from that game too. Our content creators used to focus on Call of Duty too, so when we knew that a league was coming, we knew that we had to be there because we belong to Call of Duty.We’re not a huge organisation like most of the owners of CDL franchises – we haven’t had any crazy investments rounds or anything like that – but we knew that we had to go for it. We did whatever we could to be there.RELATED: Paul Hamilton explains how Atlanta FaZe came to be ESI: So you think franchising is a good model for esports? AV: I think it’s good to control the teams that are part of the league, especially in Call of Duty. The last year was a total mess, there were a lot of organisations that didn’t bring anything to the table. There were 16 teams in the CWL Pro League and there were six or eight organisations that were actually bringing value. With a franchised league though, it totally changes things with the requirement to create new brands. That’s not something that I personally agree with but, apart from that, I think franchising is a good idea.  ESI: We’ve seen Atlanta FaZe manage to utilise FaZe Clan’s brand, does that go against what you believed was possible at the application stage? AV: That was surprising, I’m not going to lie. I read your interview with them to know how they actually managed to do that because, personally, I didn’t know it was possible. I’m sure that a lot of organisations, if they knew, would have done something similar. I guess they have their reasons for it but on our side we didn’t know that it could be done.",
