      text:
        "ANAHEIM, California -- A two-day slog ended in an unprecedented victory Saturday at BlizzCon.Cheers erupted inside the Anaheim Convention Center Arena as the six American players leapt out of their seats and were swarmed by their staff and substitutes. After four years of early-bracket exits at the Overwatch World Cup, Team USA had done it. They were the champions.Team USA featured arguably the best North American players in their roles: three from the reigning Overwatch League champions, the San Francisco Shock. The four others were standouts, if not stars, on other OWL teams.The talent translated into dominant performances, with the U.S. dropping only one map en route to their title. That loss came to three-time defending champion South Korea in a 3-1 semifinal series victory. After that win, Team USA went on to a 3-0 sweep of Team China.After a sloppy performance against Team United Kingdom in November 2018, the Americans avenged themselves this weekend with an 18-1 record on maps and not a single series loss. Well, we actually thought we're gonna 3-0 everyone, Overwatch World Cup MVP Jay Sinatraa Won said in a press conference after the team's win. But we dropped a map to Korea.This BlizzCon was an important moment for Blizzard Entertainment, which had a year beset with controversy, and for Overwatch, which on Friday announced its second installment after interest in the game had slowed down. The fourth-annual Overwatch World Cup and Team USA were the marquee draws on the esports side of BlizzCon.Another disappointing exit or upset loss in the quarterfinals would have certainly dulled the electric atmosphere in the Anaheim Convention Center Arena on Saturday and dampened Blizzard's annual celebration. This year was like a different mentality: Third time's the charm, and everyone works super hard, Team USA and Houston Outlaws support Shane Rawkus Flaherty said. It feels so good actually finally do that. Like, I don't, I don't really have words to really explain how I feel right now. But it was something that I wanted to do no matter what. Season 3 Overwatch League is going to be like a whole new challenge for us, moth said, but we're going to keep working really hard. We will never get complacent.",
