      id: 8,
      text:
        "It's rare that the League of Legends World Championship has two teams that both represent the year in its entirety. League is a game fraught with meta shifts -- the most infamous ones historically released immediately before the world championship itself -- and concentrated metas develop in miniature on the worlds stage. For a recent example: Royal Never Give Up exemplified what League of Legends was last year, yet failed to adapt to the shifting worlds meta and were eliminated in the quarterfinals in 2018 by G2 Esports. This year is different. No two teams sum up what League of Legends has been in 2019 more than FunPlus Phoenix and G2 Esports. G2 are on the cusp of claiming an elusive sweep of every major League title in a single year, something that has yet to be achieved by any team in the game's history. They've made it to this point, with a Mid-Season Invitational title and two domestic championships to their name, thanks to their remarkable flexibility. Although FunPlus haven't had that same impact, they've stubbornly stuck to their specific style of play through two successful regular-season splits. FunPlus dropped only one series during the summer series, and won China's hotly contested League of Legends Pro League title. Both of these teams know how to make the most of the talent available on their respective rosters, and this knowledge has brought them to the game's most prestigious match, which takes place at 7 a.m. ET on Sunday at AccorHotels Arena in Paris. Here are a few factors to keep in mind while watching the series. G2 have historically struggled against teams that make the most out of global ultimates, with the most recent example coming from Griffin's combination of a support Shen with two Teleports during Group A of worlds. FunPlus ran triple-Teleport in all of their games against iG and nearly all of their games against Fnatic. Additionally, FunPlus love to play champions with global or semi-global ultimates like Tahm Kench, Ryze and Gangplank. Globals don't guarantee victory against G2, but they already work well with exactly how FunPlus wants to play and help spread G2 on the map before they can set up split-pushes with their preferred stronger laning matchups.",
