      id: 9,
      text:
        "Champions will be crowned this weekend at Hi-Rez Expo, which is home to the SMITE and Paladins World Championships. Esports Insider spoke with Alex Cantatore, Brand Director of Live Games at Hi-Rez Studios to find out more about the studio’s biggest event of the year. We’re just wrapping up the first season of the SMITE Pro League played entirely on LAN here at our Atlanta esports studio, and the level of competition is just insane. These teams are living together, playing SMITE all day, and focused solely on being the best in the world. This is the best SMITE that’s ever been played, and I can’t wait to see what they pull out on stage at Hi-Rez Expo. We bring partners out to Hi-Rez Expo every year, people who attend every esport event on the planet, and they all say the same thing: “This is special.” The passion of our fans is just unbelievable, and getting to spend a weekend with them is as much of a reward to us as lifting Thor’s hammer is to the SMITE World Champions. Then we just kept growing. We added the Hi-Rez Expo in a side hall, a secondary attraction to the SMITE World Championship. And then as we added the Paladins World Championship, it just made sense to change the event’s name to be more inclusive of Hi-Rez Studios’ multiple games. It was Cognitive Prime, a North American squad with some really lovable players on it, against Titan, this upstart European amateur team that had played their way through last-chance qualifiers. Titan shouldn’t have even made it to Hi-Rez Expo; they were 5 seconds away from losing before one player popped off, killed the entire enemy team, and won the game. And Titan was down 2-0 in the World Championship finals, but once again they fought back with an unstoppable, unexpected pick: Ares support. It went to a deciding fifth game where Cog Prime finally banned the Ares and battled to raise Thor’s hammer as the first SMITE World Champions. It was really in that moment when I was convinced I had to work at Hi-Rez, I had to be a part of this.",
