      textTwo:
        "At the end of the story is an image of Mercy dressed up in (relatively) conventional doctor duds, which players can now acquire in Mercy's Recall Challenge, a new in-game event that kicked off today. Winning three games in Quick Play, Competitive, or the Overwatch Arcade will earn you a new player icon. Six victories will net a pair of new sprays, and for nine you'll take home the Dr. Ziegler legendary skin. You can also score more Mercy sprays for watching Overwatch streamers on Twitch: One spray for watching two hours of streaming, two sprays for four hours, and three new sprays for six hours of viewing. Watching in this case is kind of a flexible concept—Jeff Kaplan isn't going to check in to make sure you're actually paying attention, or even in the room—but you will need to have your Twitch and Overwatch accounts linked, which you can take care of at twitch.tv.",
