      id: 10,
      title: "PUBG uses machine learning to look for cheaters",
      text:
        "The anti-cheat team at PUBG Corp have posted an update on Steam that covers some of the work it's been doing over the last few months to stop cheat programs from disrupting the battle royale. While some specifics have to be avoided because revealing everything the team's up to could harm progress, it provides a handy overview of the issues facing an online game like PUBG, along with some of the solutions that they've come up with. ",
      textTwo:
        "Along with third party anti-cheat programs like BattlEye and Uncheater, the team uses machine learning to study how people play, using that data to keep an eye out for things outside the norm that could suggest a hack's being used. Other companies have also been brought in to help improve PUBG's security. Blocking DLL injection attacks has also been a priority for the team, but some attempts to stop cheat programs from interfering with the game during development lead to things like Steam and Discord being misidentified as a threat.Each game of PUBG sees up to 100 users trying to beat already incredible odds, PUBG Corp says. We understand that even one person playing unfairly can severely impact the fun of so many people, and thus we need to maintain a much higher level of security than other games.",
