      title: "Arma 3 is free to play this weekend, and 66% off",
      text:
        "Nothing says romance like spending time with the ones you love in a very serious military sim. Keep your flowers and sentimental gifts, I want military jargon, tense firefights and maybe the opportunity to blow up some tanks. Arma 3 is offering all of that and a heck of a lot more this weekend when it briefly sheds its price. You'll be able to play for free until Monday. ",
      textTwo:
        "I suspect most people who are into their military sims already own at least one of the Armas, but it would probably have even greater number of adherents if more people realised that it's more accessible than it first appears. That doesn't mean it's the most intuitive game, or easy to wrap your head around, but it's also not just a military shooter. It's a sandbox and platform for creating whole new modes, games and custom servers. There's even go-karting. There are some really impressive player creations, too, including this extremely fancy Halo mod. And then there's this nightmarish Lego mod. I'll never be able to shake the image of their grinning faces as they're riddled with bullets. ",
